'use strict';
 
import Vue from 'vue/dist/vue';
import {apiRoot} from '../../../../../../config.js';

let template = require('./template.html');
template     = eval(`\`${template}\``);

const formLogin = Vue.extend({
  template,
  props: {
		value:Object,
	},
	data(){
		return{
			userLogin: JSON.parse(JSON.stringify(this.value)),
			cantSubmit:false
		}
	},
	methods:{
		// la fonction de connexion renvoit sur la page d'actualité, si l'utilisateur a bien rempli ses champs et existe dans la bdd
		connect(){
			this.$emit('input', this.userLogin);

			if(this.userLogin.mail != "" | this.userLogin.password != ""){
		       	this.$http.post(apiRoot() + 'auth/login', 
		       	{
		       		'mail' : this.userLogin.mail, 
		       		'password': this.userLogin.password 
		       	},{
		        	emulateJSON: true
		        }).then(
		          (response) => {
		            this.cantSubmit=false;
		            this.$router.push({
					    name: 'Feed' 
					});
		          },
		          (response) => {
		            this.cantSubmit=true;
		          }
		        )
		    }

    	}
	      		
		
	}
});


export default formLogin;
